@media only screen and (max-width: 600px) {
  .buttonsAll {
    display: block;
  }

  .resourceButton1 {
    display: block;
    margin-bottom: 1em;
  }

  .resourceButton2 {
    display: block;
    margin-bottom: 1em;
  }

  .resourceButton3 {
    display: block;
  }
}

