  background-color: rgba(20, 172, 55, 0.8);
}

.ArrowButtons {
  text-align: center;
  display: flex;
  justify-content: space-between;
}

.ArrowButtons2 {
  text-align: center;
  display: flex;
  justify-content: space-around;
}

.parentDiv {
  display: flex;
}

.leftcol,
.rightcol {
  flex: 1;
}

.rightArrow {
  text-align: right;
  cursor: pointer;
}
.ArrowButtons:hover {
  cursor: pointer;
}
